import logo from './logo.svg';
import './App.css';
import mergeImages from 'merge-images';
import api from './utils/api';
import { triggerBase64Download } from 'react-base64-downloader';

import { useState, useEffect } from 'react';

function App() {
  const [backgroundList, setBackgroundList] = useState([]);
  const [hoodieList, setHoodieList] = useState([]);
  const [bulbList, setBulbList] = useState([]);
  const [overheadList, setOverheadList] = useState([]);
  const [hatList, setHatList] = useState([]);
  const [glassesList, setGlassesList] = useState([]);
  const [bodyList, setBodyList] = useState([]);

  const importAll = (r) => {
    let images = {};
    r.keys().map((item, index) => { images[item.replace('./', '')] = r(item); });
    return images;
  }

  useEffect(() => {
    let tmpObj = importAll(require.context('./images/background', false, /\.(png|jpe?g|svg)$/));
    setBackgroundList(Object.keys(tmpObj));

    tmpObj = importAll(require.context('./images/hoodie', false, /\.(png|jpe?g|svg)$/));
    setHoodieList(Object.keys(tmpObj));

    tmpObj = importAll(require.context('./images/bulb', false, /\.(png|jpe?g|svg)$/));
    setBulbList(Object.keys(tmpObj));

    tmpObj = importAll(require.context('./images/overhead', false, /\.(png|jpe?g|svg)$/));
    setOverheadList(Object.keys(tmpObj));

    tmpObj = importAll(require.context('./images/hat', false, /\.(png|jpe?g|svg)$/));
    setHatList(Object.keys(tmpObj));

    tmpObj = importAll(require.context('./images/glasses', false, /\.(png|jpe?g|svg)$/));
    setGlassesList(Object.keys(tmpObj));

    tmpObj = importAll(require.context('./images/body', false, /\.(png|jpe?g|svg)$/));
    setBodyList(Object.keys(tmpObj));
  }, []);

  const generateImage = (id, dnaString) => {
    let pathArray = [];

    let index = Number(dnaString.slice(0, 2));
    if (index !== 0) {
      pathArray.push(require('./images/background/' + backgroundList[index-1]));
    }

    index = Number(dnaString.slice(2, 4));
    if (index !== 0) {
      pathArray.push(require('./images/hoodie/' + hoodieList[index-1]));
    }

    index = Number(dnaString.slice(4, 6));
    if (index !== 0) {
      pathArray.push(require('./images/bulb/' + bulbList[index-1]));
    }

    index = Number(dnaString.slice(6, 8));
    if (index !== 0) {
      pathArray.push(require('./images/overhead/' + overheadList[index-1]));
    }

    index = Number(dnaString.slice(8, 10));
    if (index !== 0) {
      pathArray.push(require('./images/hat/' + hatList[index-1]));
    }

    index = Number(dnaString.slice(10, 12));
    if (index !== 0) {
      pathArray.push(require('./images/glasses/' + glassesList[index-1]));
    }

    index = Number(dnaString.slice(12, 14));
    if (index !== 0) {
      pathArray.push(require('./images/body/' + bodyList[index-1]));
    }

    mergeImages(pathArray)
      .then(b64 => {
        triggerBase64Download(b64, id.toString());
      })
  }

  const generateImageCollection = async () => {
    let res = await api.get('/dnaList');
    res.data.dnaList.map((item) => {
      generateImage(item.id, item.dna);
    })
  }

  const generateMetaCollection = () => {

  }

  return (
    <div className="App">
      <header className="App-header">
        <button style={{ height: 80, marginBottom: 30 }} onClick={generateImageCollection}>Generate Image Collection</button>
        {/*<button style={{ height: 80, marginBottom: 30 }} onClick={generateMetaCollection}>Generate Metadata Collection</button> */}
      </header>
    </div>
  );
}

export default App;
